import styled from 'styled-components';
import { Link } from 'react-router-dom';

export const Container = styled.div`
    min-height: 692px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 0;
    overflow: hidden;
    background: linear-gradient(
        108deg,
        rgba(0,166,237, 1) 0%,
        rgba(20, 56, 122, 1) 100%
    );
`

export const FormWrap = styled.div`
        height: 100%;
        display:flex; 
        flex-direction: column;
        justify-content: center;

        @media screen and (max-width: 400px){
            height: 80%;
        }

`

export const Icon = styled(Link)`
    margin-left: 32px;
    margin-top: 32px;
    text-decoration: none;
    color: #fff;
    font-weight: 700;
    font-size: 32px;

    @media screen and (max-width: 480px){
        margin-left: 16px;
        margin-top: 16px;
    }
`

export const FormContent = styled.div`
    height: 100%;
    display:flex;
    flex-direction: column;
    justify-content: center; 

    @media screen and (max-width: 480px){
        padding: 10px;
    }
`

export const Form = styled.form`
    background: rgba(0,0,0,0.4);
    max-width: 500px;
    height: auto;
    width: 100%;
    z-index: 1;
    display: grid;
    margin: 0 auto;
    padding: 80px 32px;
    border-radius: 4px;
    box-shadow: 0 2px 3px rgba (0,0,0, 0.9);

    @media screen and (max-width: 400px){
        padding: 32px 32px;
    }
`

export const FormH1 = styled.h1`
    margin-bottom: 40px;
    color: #fff;
    font-size: 20px; 
    font-weight: 400;
    text-align: center;
`

export const FormLabel = styled.label`
    margin-bottom: 8px;
    font-size: 14px; 
    color: #fff;
`

export const FormInput = styled.input`
    padding: 16px 16px;
    margin-bottom: 32px;
    border: none;
    outline: none;
    border-radius: 4px;
`

export const FormButton = styled.button`
    background: #00A6ED;
    padding: 16px 0;
    border: none;
    border-radius: 4px;
    color: #fff;
    font-size: 20px;
    cursor:pointer;

    &:hover{
        transition: 0.2s ease-in;
        background: #000;
    }
`

export const Text = styled.span`
    text-align: center;
    margin-top: 24px;
    color: #fff;

    &:hover{
        color: #00A6ED;
        cursor:pointer;
    }
`

export const TextLink = styled(Link)`
    text-align: center;
    margin-top: 12px;
    margin-bottom: 12px;
    color: #fff;
    text-decoration: none;
    border: transparent;
    font-size: 18px;

    &:hover{
        transition: 350ms ease-in;
        cursor:pointer;
        padding:15px;
        border: .05rem solid #00A6ED;
        font-size: 21px;
        background: #00A6ED;
    }

`