import React from 'react';
import { animateScroll as scroll} from 'react-scroll';
import { FaFacebook, FaInstagram, FaYoutube, FaTwitter, FaLinkedin} from 'react-icons/fa';
import { FooterContainer, FooterWrap, FooterLinksContainer, 
FooterLinksWrapper, FooterLinkBottom, FooterLinkItems, FooterLinkTitle,
FooterLink, WebsiteRights,  SocialMedia, SocialIconLink, SocialIcons, SocialLogo, SocialMediaWrap} from './FooterElements'


const Footer = () => {

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    
    return (
        <FooterContainer>
            <FooterWrap>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>About Us</FooterLinkTitle>
                                <FooterLink to="/signin">How it Works</FooterLink>
                                <FooterLink to="/signin">Testimonials</FooterLink>
                                 <FooterLink to="/signin">Careers</FooterLink>
                                <FooterLink to="/signin">Investors</FooterLink>
                                <FooterLink to="/signin">Terms of Service</FooterLink>
                        </FooterLinkItems>

                        <FooterLinkItems>
                            <FooterLinkTitle>Videos</FooterLinkTitle>
                                <FooterLink to="/signin">Submit A Video</FooterLink>
                                <FooterLink to="/signin">Video Privacy Policy</FooterLink>
                                <FooterLink to="/signin">Ambassadors</FooterLink>
                                <FooterLink to="/signin">Agency</FooterLink>
                                <FooterLink to="/signin">Influencer</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>

                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>Contact Us</FooterLinkTitle>
                                <FooterLink to="/signin">Support</FooterLink>
                                <FooterLink to="/signin">Help Center</FooterLink>
                                <FooterLink to="/signin">Destinations</FooterLink>
                                <FooterLink to="/signin">Netvink Global®</FooterLink>
                                <FooterLink to="/signin">Sponsorships</FooterLink>
                        </FooterLinkItems>

                        <FooterLinkItems>
                            <FooterLinkTitle>Social Media</FooterLinkTitle>
                                <FooterLink to="/signin">Instagram</FooterLink>
                                <FooterLink to="/signin">Facebook</FooterLink>
                                <FooterLink to="/signin">LinkedIn</FooterLink>
                                <FooterLink to="/signin">Youtube</FooterLink>
                                <FooterLink to="/signin">Twitter</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
        <SocialMedia>
            <SocialMediaWrap>
                <SocialLogo onClick={toggleHome} to='/'>
                    netvink
                </SocialLogo>
                <WebsiteRights>
                    netvink ©{new Date().getFullYear()} &nbsp;
                    All rights reserved.
                </WebsiteRights>
                <SocialIcons>
                    <SocialIconLink aria-label="Facebook" href='//www.facebook.com' target="_blank">
                        <FaFacebook/>
                    </SocialIconLink>
                    <SocialIconLink aria-label="Instagram" href='//www.instagram.com' target="_blank">
                        <FaInstagram/>
                    </SocialIconLink>
                    <SocialIconLink aria-label="Youtube" href='//www.youtube.com' target="_blank">
                        <FaYoutube/>
                    </SocialIconLink>
                    <SocialIconLink aria-label="Twitter" href='//www.twitter.com' target="_blank">
                        <FaTwitter/>
                    </SocialIconLink>
                    <SocialIconLink aria-label="Linkedin" href='//www.linkedin.com' target="_blank">
                        <FaLinkedin/>
                    </SocialIconLink>
                </SocialIcons>
            </SocialMediaWrap>
        </SocialMedia>
                <FooterLinkBottom target="_blank" href='//www.otheweb.dev'>Site created by Otheweb.dev</FooterLinkBottom>
            </FooterWrap>
        </FooterContainer>
    )
}

export default Footer
